import { StatusBar } from 'expo-status-bar';
import { StyleSheet, Text, View,Image} from 'react-native';
import {ScrollView,Dimensions} from "react-native";
import registerNNPushToken from 'native-notify';
const win=Dimensions.get('window');

export default function App(){
    let num=1;
    let n=9;
    let output=[];
    output.push(<Image source={{uri:'https://github.com/zaarzet/memes/blob/main/0.png?raw=true',}} style={styles.forImage}/> )

    for(let i=n;i>=1;i--){
        output.push(<Image source={{uri:"https://github.com/zaarzet/memes/blob/main/"+String(i)+".jpg?raw=true"}} style={styles.forImage} />)
    }
    return(

        <View style={styles.container}>
            <Text></Text>
            <Text style={[{color: "rgb(39, 255, 5)",fontSize:40,marginTop:30,backgroundColor:'black',paddingLeft:10,paddingRight:10}]}>Z A A R Z E T</Text>
            <Text style={[{color:"rgb(45,255,9)"}]}>This app is full of HighContrast (Made on purpose)</Text>
            <ScrollView>
                {output}
                <StatusBar style="auto"/>
            </ScrollView>
            <Text></Text>

        </View>
    );
}

const styles = StyleSheet.create({
    container:{
        flex: 1,
        fontSize:100,
        backgroundColor: 'black',
        alignItems: 'center',
        justifyContent: 'center',
    },
    Text:{
        fontSize:57,
        color:'white',
        //backgroundColor:'black'
    },
    forImage:{
        width:win.width/(1.1),
        height:win.width/(1.1),
        marginTop:5,
        resizeMode:"contain",
        alignSelf:"center",
        borderWidth:1,
        borderRadius:0,
    },
});
